@import url(https://fonts.googleapis.com/css?family=Lato);
html{--bg-panel: #EBEBEB;--color-headings: #333;--color-text: #999;--color-fun: #c44394;--distance: 4rem}body{font-family:'Lato', Helvetica, Arial, sans-serif;height:100vh;display:grid;place-items:center;overflow:hidden;margin:0}.card{z-index:1;background:#EBEBEB;background:var(--bg-panel);border-radius:1rem;padding:1rem;box-shadow:4px 4px 12px 2px rgba(0,0,0,0.6);margin-left:-8vw;height:14vw;width:18vw;position:relative}@media (max-width: 1024px){.selection{margin-left:-43.9vw;height:50vh;width:80vw}.rankCard{height:50vw;width:60vw;margin-left:1.7vw;margin-top:20vw}.reorder{height:20vw;width:26vw}}.stack{color:#999;color:var(--color-text);margin-left:-1rem}a{text-decoration:none;color:#333;color:var(--color-headings)}p{color:#999;color:var(--color-text)}.reorder{height:8vh;width:6vw}.stackContainer{min-width:20rem;min-height:20rem}.jumper:hover{transition:0.2s;-webkit-transform:translatey(-0.5rem);transform:translatey(-0.5rem)}.wideColumn{min-width:20rem;min-height:14rem}

